import {useFormik} from 'formik'
import * as yup from 'yup'
import React from 'react'
import axios from 'axios'
import {useNavigate} from 'react-router-dom'
import { useState } from 'react'
import styles from './Register.module.css'

export default function Register() {
   let navigate = useNavigate();
   const [loading , setLoading] = useState(false);
   
   async function handleRegister(values){

    setLoading(true)
    let {data} = await axios.post(`https://ecommerce.routemisr.com/api/v1/auth/signup  `, values)
    
    if(data.message === 'success')
    {
      setLoading(false)
      navigate('/LogIn')
      
      
    console.log(values);
   }
  }

   let validationSchema = yup.object({
    name:yup.string().required('name is required').min(3 , 'name minmum length is 3').max(10 , 'name maxmum length is 10'),
    email:yup.string().required('email is required').email('email is valid'),
    password:yup.string().required('password is required').matches(/^[A-Z][a-z0-9]{5,10}$/ , 'password must matched'),
    rePassword:yup.string().required('repassword is required').oneOf([yup.ref('password')],'must repassword matched password'),
    phone:yup.string().required('phone is required').matches(/^01[0125][0-9]{8}$/ , 'must phone number egyption')
   })
  let formik = useFormik({
     initialValues:{
      name:'',
      email:'',
      password:'',
      rePassword:'',
      phone:'',
      
      
     },
     validationSchema,
     onSubmit:handleRegister
    
  });
  return <>

    <div className='w-75 mx-auto'>
      <h3>Register :</h3>
   
      <form onSubmit={formik.handleSubmit}>
      
        
         <label htmlFor='name'> Name</label>
         <input className='form-control' onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.name} type='text' name='name' id='name' />
         {formik.errors.name && formik.touched.name  ? <div className='alert alert-danger'>{formik.errors.name}</div> : null }
        
         <label htmlFor='email'>Email</label>
         <input className='form-control' onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.email} type='email' name='email' id='email'/>
         {formik.errors.email && formik.touched.email  ? <div className='alert alert-danger'>{formik.errors.email}</div> : null }
         
         <label htmlFor='password'>Password</label>
         <input className='form-control' onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.password} type='password' name='password' id='password'/>
         {formik.errors.password && formik.touched.password  ? <div className='alert alert-danger'>{formik.errors.password}</div> : null }

         <label htmlFor='rePassword'>rePassword</label>
         <input className='form-control' onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.rePassword} type='password' name='rePassword' id='rePassword'/>
         {formik.errors.rePassword && formik.touched.rePassword  ? <div className='alert alert-danger'>{formik.errors.rePassword}</div> : null }
         
         <label htmlFor='phone'>Phone</label>
         <input className='form-control' onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.phone} type='text' name='phone' id='phone'/>
         {formik.errors.phone && formik.touched.phone  ? <div className='alert alert-danger'>{formik.errors.phone}</div> : null }
         
         {loading ?<button className='btn bg-main text-white mt-3'><i className="fas fa-spinner fa-spin "></i></button> :
           <button disabled={!(formik.isValid && formik.dirty)} type='submit' className='btn bg-main text-white mt-3 '>Register</button>

         }
          
        
        

      </form>

    </div>

  </>
   

}
