import React, { useContext, useEffect } from 'react'
import styles from './Cart.module.css'
import { cartContext } from '../../Context/cartContext'
import { useState } from 'react';
import toast from 'react-hot-toast';
import { Link } from 'react-router-dom';
export default function Cart() {
  let {getLogUserCart , removeItem , getUpDate } = useContext(cartContext);

   let [cartDetails , setCartDetails] = useState(null)


  async function getCart(){
    let response = await getLogUserCart()
    if(response?.data?.status ==='success')
    {
      setCartDetails(response.data.data);
    }
  }


  async function deleteItem( productId){
      let response = await removeItem( productId)
      setCartDetails(response?.data?.data);
      toast('done removed')
  }

  async function updatecart(productId , count)
  {
    let response = await getUpDate(productId , count)
    setCartDetails(response?.data?.data);
    toast('done')
  }
  useEffect(()=>{
     getCart();
  } , [])
  return<>
  {cartDetails !== null ? <div className='bg-main-light p-4 mt-3'>
        <h3>shop Cart :</h3>
        <h6 className='text-main'>Total Cart Price : {cartDetails.totalCartPrice} EGP</h6>
        {cartDetails?.products?.map((product)=> <div key={product.product._id} className='row border-bottom py-2' >
          <div className='col-md-1'>
              <img src={product.product.imageCover} className='w-100'/>
          </div>
          <div className='col-md-11 d-flex justify-content-between'>
             <div >
             <h6>{product.product.title}</h6>
             <h6>Price :{product.price}</h6>
             <button onClick={()=>deleteItem(product.product._id)} className='btn p-0 m-0'><i className='fa-regular fa-trash-can text-center'></i>Remove</button>
             </div>
             <div>
              <button className=' border-main ' onClick={()=> updatecart(product.product._id , product.count+1)}>+</button>
               <span className='mx-2'>{product.count}</span>
              <button className=' border-main ' onClick={()=> updatecart(product.product._id , product.count-1)}> -</button>
             </div>
          </div>
        </div>)}
        <button className='btn bg-main mt-2'>
          <Link to={'/checkout'} className='bg-main text-white '>checkout</Link>
        </button>
     </div>: null
  }
    
  
  
  
  
  </>
}
