import logo from './logo.svg';
import toast, { Toaster } from 'react-hot-toast';
import LayOut from './Components/LayOut/LayOut';
import Home from './Components/Home/Home';
import Cart from './Components/Cart/Cart';
import Categories from './Components/Categories/Categories';
import Products from './Components/Products/FeaturedProducts';
import ProductDetails from './Components/ProductDetails/ProductDetails';
import Brands from './Components/Brands/Brands';
import About from './Components/About/About';
import LogIn from './Components/LogIn/LogIn';
import Register from './Components/Register/Register';
import Notfound from './Components/Notfound/Notfound';
import { RouterProvider, createBrowserRouter } from 'react-router-dom';
import { useState } from 'react';
import { jwtDecode } from "jwt-decode";
import './App.css';
import ProductedRoute from './Components/ProductedRoute/ProductedRoute';
import { CartContextProvider } from './Context/cartContext';
import Checkout from './Components/checkout/checkout';

function App() {

  const[userData , setuserData] = useState(null)
  function  SaveUserData(){
    let encoded = localStorage.getItem('userToken');
    let decoded = jwtDecode(encoded);
    setuserData(decoded)
  }
   let Router = createBrowserRouter ([
    {path:'' , element:<LayOut setuserData={setuserData} userData={userData}/> , children:[
      {index:true , element :<ProductedRoute><Home/></ProductedRoute>},
      {path:'About', element :<ProductedRoute><About/></ProductedRoute>},
      {path:'Cart', element : <ProductedRoute><Cart/></ProductedRoute>},
      {path:'checkout', element : <ProductedRoute><Checkout/></ProductedRoute>},
      {path:'Categories', element : <ProductedRoute><Categories/></ProductedRoute>},
      {path:'Products', element : <ProductedRoute><Products/></ProductedRoute>},
      {path:'ProductDetails/:id', element : <ProductedRoute><ProductDetails/></ProductedRoute>},
      {path:'Brands', element : <ProductedRoute><Brands/></ProductedRoute>},
      {path:'logIn', element :<LogIn SaveUserData={SaveUserData}/>},
      {path:'Register', element :<Register/>},
      {path:'LogOut', element :<LayOut/>},
      {path:'*', element :<Notfound/>},
    ]}

   ])
  return <CartContextProvider>
            <Toaster/>
            <RouterProvider router={Router}></RouterProvider>
  </CartContextProvider>
 


}

export default App;
