import React, { useContext } from 'react'
import styles from './checkout.module.css'
import { useFormik } from 'formik'
import { cartContext } from '../../Context/cartContext';


export default function Checkout() {
   let{onlinePaymint} = useContext(cartContext);

    async function handleSubmit(values)
    {
        let response = await onlinePaymint('65ad819336147b0033b7eccf' , values)
        if(response?.data?.status === 'success')
        console.log(response.data.session.url);
        window.location.href = response.data.session.url;
        console.log(response);

    }
    let formik = useFormik({
        initialValues:{
           details: '',
           phone:'',
           city:''
        },
        onSubmit:handleSubmit
    
    })
    
     
  return <>
    
    <div className='w-50 mx-auto py-5'>
         <form onSubmit={formik.handleSubmit}>
            <label htmlFor='details'>details :</label>
            <input name='details' id='details' type='text' className='form-control' value={formik.values.details} onChange={formik.handleChange}/>
            
            <label htmlFor='phone'>phone :</label>
            <input name='phone' id='phone' type='text' className='form-control' value={formik.values.phone} onChange={formik.handleChange}/>

            <label htmlFor='city'>city :</label>
            <input name='city' id='city' type='text' className='form-control' value={formik.values.city} onChange={formik.handleChange}/>

            <button type='submit' className='btn w-100 border-main mt-3'>pay</button>

         </form> 
    </div>
  
  
  </>
}
