import React from 'react'
import styles from './LogIn.module.css'
import {useFormik} from 'formik'
import * as yup from 'yup'
import axios from 'axios'
import {useNavigate} from 'react-router-dom'
import { useState } from 'react'
export default function LogIn({SaveUserData}) {

  
    let navigate = useNavigate();
    const [loading , setLoading] = useState(false);
    const [messageError , setmessageError] = useState('');

     async function handleLogIn(values){
  
      setLoading(true)
      let {data} = await axios.post(`https://ecommerce.routemisr.com/api/v1/auth/signin`, values).catch((err)=>{
       setmessageError(`${err.response.data.errors.param} : ${err.response.data.errors.msg}`)
      })
      
      if(data.message === 'success')
      {
        localStorage.setItem('userToken' , data.token)
        SaveUserData()
        setLoading(false);
        navigate('/')
        
        
     }
    }
  
     let validationSchema = yup.object({
     
      email:yup.string().required('email is required').email(),
      password:yup.string().required('password is required').matches(/^[A-Z][a-z0-9]{5,10}$/ , 'password must matched'),
      
     })
    let formik = useFormik({
       initialValues:{
    
        email:'',
        password:'',
      
        
        
       },
       validationSchema,
       onSubmit:handleLogIn
      
    });
    return <>
  
      <div className='w-75 mx-auto'>
        <h3>Login :</h3>
         {messageError.length > 0?<div className='alert alert-danger'>{messageError}</div>:null}


        <form onSubmit={formik.handleSubmit}>
        
          
           <label htmlFor='email'>Email</label>
           <input className='form-control' onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.email} type='email' name='email' id='email'/>
           {formik.errors.email && formik.touched.email  ? <div className='alert alert-danger'>{formik.errors.email}</div> : null }
           
           <label htmlFor='password'>Password</label>
           <input className='form-control' onBlur={formik.handleBlur} onChange={formik.handleChange} value={formik.values.password} type='password' name='password' id='password'/>
           {formik.errors.password && formik.touched.password  ? <div className='alert alert-danger'>{formik.errors.password}</div> : null }
  
           
           {loading ? <button className='btn bg-main text-white mt-3'><i className="fas fa-spinner fa-spin "></i></button> :
             <button disabled={!(formik.isValid && formik.dirty)} type='submit' className='btn bg-main text-white mt-3 '>LogIn</button>
  
           }
            
          
          
  
        </form>
  
      </div>
  
    </>
     
  
  
  
}
