import axios from "axios";
import { createContext } from "react";



 export let cartContext = createContext();


export function CartContextProvider(props){
     let headers ={
       token: localStorage.getItem('userToken')
     }
    function addToCart(x){
       return axios.post(`https://route-ecommerce.onrender.com/api/v1/cart` ,
        {
            productId:x
        },
        {
            headers:headers
        }).then((response)=>response)
        .catch((error) => error)
    
    }
   
    function getLogUserCart(x){
        return axios.get(`https://route-ecommerce.onrender.com/api/v1/cart` , {
            headers:headers
        }).then((response)=> response)
        .catch((error)=>error)
    }

    function removeItem(productId){
        return axios.delete(`https://route-ecommerce.onrender.com/api/v1/cart/${productId}` ,
         {
            headers:headers
        }).then((response)=> response)
        .catch((error)=>error)
    }

    function getUpDate(productId , count){
        return axios.put(`https://route-ecommerce.onrender.com/api/v1/cart/${productId}` , 
        {
           count:count
        },
        {
            headers:headers
        }).then((response)=>response)
        .catch((error)=> error)
    }
    
    function onlinePaymint(cartId , shippingAddress){
        return axios.post(`https://route-ecommerce.onrender.com/api/v1/orders/checkout-session/${cartId}?url=http://localhost:3000` , 
        {
            shippingAddress:shippingAddress
        },
        {
            headers:headers
        }).then((response)=>response)
        .catch((error)=> error)

    }



    return <cartContext.Provider value={{addToCart , getLogUserCart , removeItem , getUpDate , onlinePaymint }}>
       {props.children}
    </cartContext.Provider>
}